import { Calendar, Home, Inbox, Search, Settings } from "lucide-react";
import {
  Sidebar,
  SidebarContent,
  SidebarGroup,
  SidebarGroupContent,
  SidebarGroupLabel,
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
} from "./ui/sidebar";
import Link from "next/link";
import Image from "next/image";

import logo from "@/public/logo.png";
import { Separator } from "./ui/separator";

// Menu items.
const items = [
  {
    title: "Dashboard",
    url: "/",
    icon: Home,
  },
  {
    title: "Inbox",
    url: "#",
    icon: Inbox,
  },
  {
    title: "Calendar",
    url: "#",
    icon: Calendar,
  },
  {
    title: "Search",
    url: "#",
    icon: Search,
  },
  {
    title: "Profile",
    url: "/profile",
    icon: Settings,
  },
];

export function AppSidebar() {
  return (
    <Sidebar>
      <SidebarContent className="bg-black w-full px- mt-0 pt-0">
        {/* <div className="bg-green-400 py-8 w-full">
          <Separator className="bg-yellow-700 h-8" />
        </div> */}

        <SidebarGroup>
          <SidebarGroupLabel className="bg-amber-50 ">
            {" "}
            <Image
              src={logo}
              width={232}
              height={232}
              alt="logo"
              className=""
            />
          </SidebarGroupLabel>
          <SidebarGroupContent className="bg-red-200">
            <div className="bg-white w-full h-4 mt-8"></div>
            <SidebarMenu className="mt-8">
              {items.map((item) => (
                <SidebarMenuItem key={item.title}>
                  <SidebarMenuButton
                    className="rounded-[2px] bg-amber-00 py-7"
                    asChild
                  >
                    <Link className="text-xl font-bold" href={item.url}>
                      <item.icon className="w-[14px]" />
                      <span>{item.title}</span>
                    </Link>
                  </SidebarMenuButton>
                </SidebarMenuItem>
              ))}
            </SidebarMenu>
          </SidebarGroupContent>
        </SidebarGroup>
      </SidebarContent>
    </Sidebar>
  );
}
